import { message } from "antd";
import { useEffect, useState } from "react"
import { courseOutsideEnum } from ".";
import { fetchCourses, ICourse, modifyCourse } from "../../servers/course";


export const useCourses = (count: number) => {
    const [courses, setCourses] = useState<ICourse[]>([]);
    const [loading, setLoading] = useState(false);
    useEffect(() => {
        setLoading(true);
        fetchCourses().then((v) => {
            const newData = v?.data_list?.map(v => {
                if (v?.course_ext_info?.course_outside) {
                    return {
                        course_outside: 'True',
                        course_link: v.course_ext_info.course_link,
                        id: v.id,
                        org: v.org,
                        display_name: v.display_name,
                        isModify: false
                    }
                } else {
                    return {
                        id: v.id,
                        org: v.org,
                        display_name: v.display_name,
                        course_outside: 'False',
                        course_link: '',
                        isModify: false
                    }
                }
            })
            setCourses(newData);
        }).finally(() => {
            setLoading(false);
        })
    }, [count]);

    return {
        loading,
        courses,
        setCourses,
        setLoading
    }
}

export const modifyCourseAction = async (item: ICourse) => {
    if (item.course_outside === courseOutsideEnum.True && !item.course_link) {
        message.error('请输入课程跳转地址');
        return;
    }
    const params = {
        course_overview: item.id,
        course_outside: item.course_outside,
        course_link: item.course_link
    }
    const res = await modifyCourse(params);
    return res.executed;
}
