import React, { useState } from "react";
import { Wrap, } from "./styled";
import { Table, Input, Popconfirm, Button as AntButton } from 'antd';
import { Button } from "../../components/Button";
import LoadingPage from "../../components/loading";
import { deleteCourse, ICourse } from "../../servers/course";
import { modifyCourseAction, useCourses, } from "./action";
import { CustomSelect } from "../../components/CustomSelect";

export enum courseOutsideEnum {
  True = 'True',
  False = 'False'
}

const courseOutsideList = [
  {
    key: 'True',
    value: '是'
  },
  {
    key: 'False',
    value: '否'
  }
]

export default () => {

  const [count, setCount] = useState(0);

  const { courses, loading, setLoading, setCourses } = useCourses(count);


  const handleChangeValue = (key: keyof ICourse, value: any, item: ICourse) => {
    const newData = courses.map(v => {
      if (v.id === item.id) {
        return { ...v, [key]: value };
      }
      return v;
    });
    setCourses(newData);
  }

  const columns = [
    {
      title: '机构名称',
      dataIndex: 'org',
      key: 'org',
    },
    {
      title: '课程名称',
      dataIndex: 'display_name',
      key: 'display_name',
    },
    {
      title: '是否直接跳转',
      dataIndex: 'course_outside',
      key: 'course_outside',
      render: (value: string, item: ICourse) => {
        if (item.isModify) {
          return <CustomSelect
            initValue={value}
            selectData={courseOutsideList}
            onChange={(key) => {
              handleChangeValue('course_outside', key, item)
            }}
          />
        } else {
          return courseOutsideList.find(v => v.key === value)?.value;
        }
      }
    },
    {
      title: '课程跳转地址',
      dataIndex: 'course_link',
      key: 'course_link',
      render: (value: string, item: ICourse) => {
        if (item.isModify) {
          return <Input
            allowClear
            className='auto-table-input'
            placeholder='请填写'
            value={value}
            onChange={(e) => { handleChangeValue('course_link', e.target.value, item) }}
          />
        } else {
          return value ? value : '-';
        }
      }
    },
    {
      title: '操作',
      key: 'operate',
      render: (item: ICourse) => {
        if (item.isModify) {
          return <>
            <Button
              text='确定'
              style={{ marginRight: 10 }}
              onPress={async () => {
                const res = await modifyCourseAction(item);
                if (res) {
                  setCount(v => v + 1);
                }
              }}
            />
            <Button
              text='取消'
              onPress={async () => {
                setCount(v => v + 1);
              }}
            />
          </>
        }
        return <>
          <Button
            text='修改'
            style={{ marginRight: 10 }}
            onPress={async () => {
              const newData = courses.map(v => v.id === item.id ? { ...v, isModify: true } : v);
              setCourses(newData);
            }}
          />
          {/* <Popconfirm
            title="确定删除此学生？"
            placement="topLeft"
            transitionName=''
            onConfirm={async () => {
              setLoading(true);
              const res = await deleteCourse(item.id);
              setLoading(false);
              if (res.executed) {
                setCount(v => v + 1);
              }
            }}
          >
            <AntButton>删除</AntButton>
          </Popconfirm> */}

        </>
      }
    },
  ];


  return (
    <Wrap>
      <Table
        pagination={{
          hideOnSinglePage: true,
        }}
        rowKey={(item) => item.id}
        dataSource={courses}
        columns={columns.map(v => ({ ...v, align: 'center' }))}
      />
      <LoadingPage isLoading={loading} />
    </Wrap>
  )
};
