import { Input, Popconfirm, Table, Button as AntButton } from "antd";
import React, { useState } from "react";
import { Button } from "../../components/Button";
import { CustomSelect } from "../../components/CustomSelect";
import { ButtonWrap } from './styled';
import { useCourseDetail, ICourseDetail, modifyCourseDetailAction } from "./action";
import LoadingPage from "../../components/loading";
import { deleteCourseDetail, IListItem } from "../../servers/students";



interface ICourseProps {
    id: number,
    courseList: IListItem[],
    serviceList: IListItem[],
}


export const Course: React.FC<ICourseProps> = ({ id, courseList, serviceList }) => {
    const [current, setCurrent] = useState(1)

    const [count, setCount] = useState(0);

    const { loading, setLoading, data, setData } = useCourseDetail(count, id);

    const handleChangeValue = (key: keyof ICourseDetail, value: any, item: ICourseDetail) => {
        const newData = data.map(v => {
            if (v.id === item.id) {
                return { ...v, [key]: value };
            }
            return v;
        });
        setData(newData);
    }

    const columns = [
        {
            title: '学校编号',
            dataIndex: 'course_school_code',
            key: 'course_school_code',
            render: (value: string, item: ICourseDetail) => {
                if (item.isModify) {
                    return <Input
                        className='auto-table-input'
                        placeholder='请填写'
                        value={value}
                        onChange={(e) => { handleChangeValue('course_school_code', e.target.value, item) }}
                    />
                } else {
                    return value;
                }
            }
        },
        {
            title: '课程名称',
            dataIndex: 'course_id',
            key: 'course_id',
            render: (value: string, item: ICourseDetail) => {
                if (item.isModify) {
                    return <CustomSelect
                        initValue={value}
                        selectData={courseList}
                        onChange={(key) => {
                            handleChangeValue('course_id', key, item)
                        }}
                    />
                } else {
                    return courseList.find(v => v.key === value)?.value;
                }
            }
        },
        {
            title: '账号',
            dataIndex: 'course_user_name',
            key: 'course_user_name',
            render: (value: string, item: ICourseDetail) => {
                if (item.isModify) {
                    return <Input
                        className='auto-table-input'
                        placeholder='请填写'
                        value={value}
                        onChange={(e) => { handleChangeValue('course_user_name', e.target.value, item) }}
                    />
                } else {
                    return value;
                }
            }
        },
        {
            title: '密码',
            dataIndex: 'course_user_password',
            key: 'course_user_password',
            render: (value: string, item: ICourseDetail) => {
                if (item.isModify) {
                    return <Input
                        className='auto-table-input'
                        placeholder='请填写'
                        value={value}
                        onChange={(e) => { handleChangeValue('course_user_password', e.target.value, item) }}
                    />
                } else {
                    return value;
                }
            }
        },
        {
            title: '生效日期',
            dataIndex: 'created',
            key: 'created',
        },
        {
            title: '失效日期',
            dataIndex: 'ended_date',
            key: 'ended_date',
        },
        {
            title: '客服',
            dataIndex: 'customer_service',
            key: 'customer_service',
            render: (value: string, item: ICourseDetail) => {
                if (item.isModify) {
                    return <CustomSelect
                        initValue={value}
                        selectData={serviceList}
                        onChange={(key) => {
                            handleChangeValue('customer_service', key, item)
                        }}
                    />
                } else {
                    return serviceList.find(v => v.key === value)?.value;
                }
            }
        },
        {
            title: '操作',
            key: 'name5',
            render: (item: ICourseDetail) => {
                if (item.isModify) {
                    return <>
                        <Button
                            text='确定'
                            style={{ marginRight: 10 }}
                            onPress={async () => {
                                const params = {
                                    ...item,
                                    stu_id: id
                                }
                                const res = await modifyCourseDetailAction(params);
                                if (res) {
                                    setCount(v => v + 1);
                                }
                            }}
                        />
                        <Button
                            text='取消'
                            onPress={async () => {
                                setCount(v => v + 1);
                            }}
                        />
                    </>
                }
                return <>
                    <Button
                        style={{ marginRight: 10 }}
                        text='修改'
                        onPress={async () => {
                            const newData = data.map(v => v.id === item.id ? { ...v, isModify: true } : v);
                            setData(newData);
                        }}
                    />
                    <Popconfirm
                        title="确定删除此学生？"
                        placement="topLeft"
                        transitionName=''
                        onConfirm={async () => {
                            setLoading(true);
                            const res = await deleteCourseDetail({ stu_id: id, id: item.id });
                            setLoading(false);
                            if (res.executed) {
                                setCount(v => v + 1);
                            }
                        }}
                    >
                        <AntButton>删除</AntButton>
                    </Popconfirm>

                </>
            }
        },
    ];
    return (
        <div style={{ paddingTop: '30px' }}>
            <ButtonWrap>
                <Button onPress={async () => {
                    setCurrent(1);
                    setData(v => [{
                        id: 0,
                        course_id: '',
                        course_user_name: "",
                        course_user_password: '',
                        course_school_code: '',
                        created: '',
                        ended_date: "",
                        customer_service: '',
                        isModify: true
                    }, ...v,])
                }}
                    text='新增学生课程'
                />
            </ButtonWrap>
            <Table
                pagination={{
                    hideOnSinglePage: true,
                    current: current,
                    onChange: (page) => {
                        setCurrent(page);
                    }
                }}
                rowKey={(item) => item.id}
                dataSource={data}
                columns={columns.map(v => ({ ...v, align: 'center' }))}
            />
            <LoadingPage isLoading={loading} />
        </div>
    );
};
