import { message } from "antd";
import { useEffect, useState } from "react"
import {
    fetchStudents,
    IStudent,
    modifyStudent,
    fetchCourseDetail,
    getCoursesList,
    modifyCourseDetail,
    modifyCourseDetailParams,
    IListItem,
    getServicesList
} from "../../servers/students";
import { validatePhone } from "../../utils/validate";


export interface IItem {
    id: number,
    username: string,
    password: string,
    phone_number: string,
    web_accelerator_name: string,
    web_accelerator_link: string,
    isModify: boolean,
}

export interface ICourseDetail {
    course_id: string
    course_school_code: string
    course_user_name: string
    course_user_password: string
    created: string
    customer_service: string
    ended_date: string
    id: number,
    isModify: boolean,
}

export const useStudents = (count: number) => {
    const [data, setData] = useState<IItem[]>([]);
    const [loading, setLoading] = useState(false);
    useEffect(() => {
        setLoading(true);
        fetchStudents().then((v) => {
            const newData = v?.data_list?.map(v => (
                {
                    ...v.user,
                    phone_number: v.phone_number,
                    web_accelerator_name: v.web_accelerator_name,
                    web_accelerator_link: v.web_accelerator_link,
                    isModify: false,
                }
            ))
            newData.reverse();
            setData(newData);
        }).finally(() => {
            setLoading(false);
        })
    }, [count]);

    return {
        loading,
        data,
        setData,
        setLoading
    }
}

export const modifyStudentAction = async (params: IStudent) => {
    console.log(params);
    if (!params.username) {
        message.error('请输入用户名');
        return;
    }
    if (!params.password) {
        message.error('请输入密码');
        return;
    }
    const phoneTemp = validatePhone(params.phone_number);
    if (!phoneTemp.state) {
        message.error(phoneTemp.msg);
        return;
    }
    const res = await modifyStudent(params);
    return res.executed;
}

export const useCourseDetail = (count: number, id: number) => {
    const [data, setData] = useState<ICourseDetail[]>([]);
    const [loading, setLoading] = useState(false);
    useEffect(() => {
        setLoading(true);
        fetchCourseDetail(id).then((v) => {
            const newData = v?.data_list
            newData.reverse();
            setData(newData);
        }).finally(() => {
            setLoading(false);
        })
    }, [count, id]);

    return {
        loading,
        data,
        setData,
        setLoading
    }
}

export const modifyCourseDetailAction = async (params: modifyCourseDetailParams) => {
    console.log(params);
    if (!params.course_id) {
        message.error('请选择课程名称');
        return;
    }
    if (!params.course_user_name) {
        message.error('请输入账号');
        return;
    }
    if (!params.course_user_name) {
        message.error('请输入密码');
        return;
    }
    const res = await modifyCourseDetail(params);
    return res.executed;
}


export const useCourseList = () => {
    const [courseList, setCourseList] = useState<IListItem[]>([]);
    useEffect(() => {
        getCoursesList().then((res) => {
            const temp = res.data_list.map(v => ({
                key: v.id,
                value: v.display_name
            }));
            setCourseList(temp);
        })
    }, []);

    return {
        courseList
    }
}

export const useServiceList = () => {
    const [serviceList, setServiceList] = useState<IListItem[]>([]);
    useEffect(() => {
        getServicesList().then((res) => {
            const temp = res.data_list.map(v => ({
                key: v.id,
                value: v.customer_service_name
            }));
            setServiceList(temp);
        })
    }, []);

    return {
        serviceList
    }
}